* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.listCountry {
  display: flex;
  margin-top: 10px;
}

.counrtyName {
  margin-left: 20px;
  font-family: 'Roboto';
  font-size: 20px;
  font-weight: 700;
}

.capital,
.population,
.languages {
  font-size: 20px;
  font-weight: 700;
}
